import React, { useState, useEffect, useRef } from "react";
import ReactPlayer from "react-player";
import Hls from "hls.js";
import "@fortawesome/fontawesome-free/css/all.min.css";
import bannertv from "./logog.png"; // Importa el logo
import fondo from "./fondo.jpg"; // Importa el fondo

function App() {
  const [categories, setCategories] = useState({});
  const [selectedCategory, setSelectedCategory] = useState("");
  const [currentUrl, setCurrentUrl] = useState("");
  const [source, setSource] = useState("genius");
  const [sidebarsVisible, setSidebarsVisible] = useState(true); // Control para barras laterales
  const [mostrarPopup, setMostrarPopup] = useState(false); // Estado para el popup de ajustes
  const [isLoading, setIsLoading] = useState(true);
  const [hasError, setHasError] = useState(false);
  const videoRef = useRef(null);
  const hlsInstance = useRef(null);
  const GENIUS_URL =
    "https://raw.githubusercontent.com/Franco2070/Genius/refs/heads/main/canalesdebolivia.m3u";

  useEffect(() => {
    if (source === "genius") fetchM3U(GENIUS_URL);
  }, [source]);

  useEffect(() => {
    if (currentUrl.endsWith(".m3u8")) setUpHLS(currentUrl);
  }, [currentUrl]);

  useEffect(() => {
    return () => {
      if (hlsInstance.current) {
        hlsInstance.current.destroy();
        hlsInstance.current = null;
      }
    };
  }, []);

  const fetchM3U = async (url) => {
    try {
      const response = await fetch(url);
      const content = await response.text();
      parseM3U(content);
      setHasError(false);
    } catch (error) {
      console.error("Error al cargar la lista:", error);
      setHasError(true);
    }
  };

  const parseM3U = (content) => {
    const lines = content.split("\n");
    const parsedCategories = {};

    for (let i = 0; i < lines.length; i++) {
      const line = lines[i].trim();
      if (line.startsWith("#EXTINF")) {
        const groupMatch = line.match(/group-title="(.*?)"/);
        const nameMatch = line.split(",").pop();
        const url = lines[i + 1]?.trim();

        if (groupMatch && url) {
          const group = groupMatch[1];
          if (!parsedCategories[group]) parsedCategories[group] = [];
          parsedCategories[group].push({ name: nameMatch, url });
        }
      }
    }
    setCategories(parsedCategories);

    const firstCategory = Object.keys(parsedCategories)[0];
    if (firstCategory) {
      setSelectedCategory(firstCategory);
      const firstChannel = parsedCategories[firstCategory][0];
      if (firstChannel) setCurrentUrl(firstChannel.url);
    }
  };

  const setUpHLS = (url) => {
    if (hlsInstance.current) hlsInstance.current.destroy();

    if (Hls.isSupported()) {
      const hls = new Hls();
      hls.loadSource(url);
      hls.attachMedia(videoRef.current);
      hls.on(Hls.Events.ERROR, (event, data) => {
        console.error("Error de HLS:", data);
        setHasError(true);
      });
      hls.on(Hls.Events.MANIFEST_PARSED, () => setIsLoading(false));
      hlsInstance.current = hls;
    } else if (videoRef.current.canPlayType("application/vnd.apple.mpegurl")) {
      videoRef.current.src = url;
    } else {
      console.error("HLS no es compatible.");
      setHasError(true);
    }
  };

  const handleFileUpload = (event) => {
    const file = event.target.files[0];
    if (file) {
      const reader = new FileReader();
      reader.onload = (e) => parseM3U(e.target.result);
      reader.readAsText(file);
    }
  };
  const abrirPopup = () => setMostrarPopup(true);
  const cerrarPopup = () => setMostrarPopup(false);

  return (
    <div
      style={{
        position: "relative",
        height: "100vh",
        overflow: "hidden",
        backgroundImage: `url(${fondo})`,
        backgroundSize: "cover",
        backgroundPosition: "center",
      }}
    >
      {/* Menú lateral */}
      {sidebarsVisible && (
        <div
          style={{
            position: "absolute",
            top: "0px",
            left: 0,
            width: "50px",
            height: "calc(100% - 10px)",
            background: "#222",
            color: "white",
            textAlign: "center",
            zIndex: 3,
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
            paddingTop: "15px",
          }}
        >

          {/* LOGO */}
          <div style={{ marginBottom: "15px", textAlign: "center" }}>
            <img src={bannertv} alt="Banner TV" style={{ height: "50px" }} />
          </div>

          {/* Inicio */}
          <div style={{ marginBottom: "15px", textAlign: "center" }}>
            <i className="fa fa-home" style={{ fontSize: "20px", marginBottom: "5px" }}></i>
            <div style={{ fontSize: "10px" }}>Inicio</div>
          </div>

          {/* Live */}
          <div style={{ marginBottom: "15px", textAlign: "center" }}>
            <i className="fa fa-tv" style={{ fontSize: "20px", marginBottom: "5px" }}></i>
            <div style={{ fontSize: "10px" }}>Live</div>
          </div>

          {/* Películas */}
          <div style={{ marginBottom: "15px", textAlign: "center" }}>
            <i className="fa fa-film" style={{ fontSize: "20px", marginBottom: "5px" }}></i>
            <div style={{ fontSize: "10px" }}>Movie</div>
          </div>

          {/* Configuraciones */}
          <div
            style={{ textAlign: "center", cursor: "pointer" }}
            onClick={abrirPopup}
          >
            <i className="fa fa-cog" style={{ fontSize: "20px", marginBottom: "5px" }}></i>
            <div style={{ fontSize: "10px" }}>Ajustes</div>
          </div>

        </div>
      )}
      {/* Botón para ocultar/mostrar ambas barras */}
      <div
        style={{
          position: "absolute",
          top: "5%",
          left: sidebarsVisible ? "380px" : "50px",
          transform: "translateY(-10%)",
          background: "#ff0000",
          color: "white",
          width: "30px",
          height: "30px",
          borderRadius: "50%",
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          cursor: "pointer",
          zIndex: 4,
        }}
        onClick={() => setSidebarsVisible(!sidebarsVisible)}
      >
        <i className={`fa ${sidebarsVisible ? "fa-chevron-left" : "fa-chevron-right"}`}></i>
      </div>
      {/* Botones de control de canal */}
      <div
        style={{
          position: "absolute",
          top: "25%",
          left: sidebarsVisible ? "380px" : "50px",
          transform: "translateY(-10%)",
          display: "flex",
          flexDirection: "column",
          gap: "10px",
          zIndex: 4,
        }}
      >
        {/* Botón Canal Anterior */}
        <div
          style={{
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
            cursor: "pointer",
          }}
          onClick={() => {
            const currentIndex = categories[selectedCategory]?.findIndex(
              (channel) => channel.url === currentUrl
            );
            if (currentIndex > 0) {
              const previousChannel = categories[selectedCategory][currentIndex - 1];
              if (currentUrl !== previousChannel.url) {
                setCurrentUrl(previousChannel.url); // Cambiar al canal anterior
                setIsLoading(true); // Reiniciar el estado de carga
                setHasError(false); // Reiniciar el estado de error
              }
            }
          }}
        >
          <div
            style={{
              background: "#007BFF",
              color: "white",
              width: "30px",
              height: "30px",
              borderRadius: "50%",
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            <i className="fa fa-arrow-left"></i>
          </div>
          <div style={{ marginTop: "5px", color: "#007BFF", fontSize: "8px" }}>
            ANTERIOR
          </div>
        </div>


        {/* Botón Siguiente Canal */}
        <div
          style={{
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
            cursor: "pointer",
          }}
          onClick={() => {
            const currentIndex = categories[selectedCategory]?.findIndex(
              (channel) => channel.url === currentUrl
            );
            if (
              currentIndex !== -1 &&
              currentIndex < categories[selectedCategory]?.length - 1
            ) {
              const nextChannel = categories[selectedCategory][currentIndex + 1];
              if (currentUrl !== nextChannel.url) {
                setCurrentUrl(nextChannel.url); // Cambiar al siguiente canal
                setIsLoading(true); // Reiniciar el estado de carga
                setHasError(false); // Reiniciar el estado de error
              }
            }
          }}
        >
          <div
            style={{
              background: "#28A745",
              color: "white",
              width: "30px",
              height: "30px",
              borderRadius: "50%",
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            <i className="fa fa-arrow-right"></i>
          </div>
          <div style={{ marginTop: "5px", color: "#28A745", fontSize: "8px" }}>
            SIGUIENTE
          </div>
        </div>

        {/* Botones de control de volumen */}
        <div
          style={{
            display: "flex",
            flexDirection: "column",
            gap: "5px",
            marginTop: "5px", // Espaciado extra debajo de los botones de canal
          }}
        >
        </div>
      </div>

      {/* Barra de categorías */}
      {sidebarsVisible && (
        <div
          style={{
            position: "absolute",
            top: "0px",
            left: "50px",
            width: "150px",
            height: "calc(100% - 10px)",
            background: "#0b090a",
            overflowY: "auto",
            boxShadow: "2px 0 5px rgba(0,0,0,0.1)",
            zIndex: 2,
          }}
        >
          <ul style={{ listStyle: "none", padding: 0 }}>
            {Object.keys(categories).map((category) => (
              <li
                key={category}
                onClick={() => setSelectedCategory(category)}
                style={{
                  padding: "10px",
                  cursor: "pointer",
                  background: selectedCategory === category ? "#e85d04" : "transparent",
                  borderBottom: "1px solid #ccc",
                  color: selectedCategory === category ? "black" : "white",
                  fontWeight: selectedCategory === category ? "bold" : "normal",
                }}
              >
                {category}
              </li>
            ))}
          </ul>
        </div>
      )}
      {/* Barra de canales */}
      {sidebarsVisible && selectedCategory && (
        <div
          style={{
            position: "absolute",
            top: "0px",
            left: "200px",
            width: "180px",
            height: "calc(100% - 10px)",
            background: "#161a1d",
            overflowY: "auto",
            boxShadow: "2px 0 5px rgba(0,0,0,0.1)",
            zIndex: 2,
          }}
        >
          <ul style={{ listStyle: "none", padding: 0 }}>
            {categories[selectedCategory]?.map((channel, index) => (
              <li
                key={index}
                onClick={() => {
                  if (currentUrl !== channel.url) {
                    setCurrentUrl(channel.url);
                    setIsLoading(true); // Reiniciar el estado de carga al cambiar
                    setHasError(false); // Reiniciar el estado de error
                  }
                }}
                style={{
                  padding: "10px",
                  cursor: "pointer",
                  background: currentUrl === channel.url ? "#f48c06" : "transparent",
                  color: currentUrl === channel.url ? "#000" : "#fff",
                  fontWeight: currentUrl === channel.url ? "bold" : "normal",
                  borderBottom: "1px solid #ccc",
                }}
              >
                {channel.name}
              </li>
            ))}
          </ul>
        </div>
      )}
      {/* Popup de ajustes */}
      {mostrarPopup && (
        <>
          <div
            style={{
              position: "fixed",
              top: "50%",
              left: "50%",
              transform: "translate(-50%, -50%)",
              background: "#222",
              color: "white",
              padding: "20px",
              boxShadow: "0 4px 8px rgba(0, 0, 0, 0.2)",
              zIndex: 1000,
            }}
          >
            <h3>Ajustes</h3>
            <label style={{ marginRight: "10px" }}>
              <input
                type="radio"
                name="source"
                value="genius"
                checked={source === "genius"}
                onChange={() => setSource("genius")}
                style={{ marginRight: "5px" }}
              />
              GENIUS TV
            </label>
            <label>
              <input
                type="radio"
                name="source"
                value="file"
                checked={source === "file"}
                onChange={() => setSource("file")}
                style={{ marginRight: "5px" }}
              />
              Lista.m3u
              {source === "file" && (
                <input
                  type="file"
                  accept=".m3u"
                  onChange={handleFileUpload}
                  style={{
                    marginLeft: "10px",
                    background: "#fff",
                    color: "#000",
                    cursor: "pointer",
                  }}
                />
              )}
            </label>
            <p>
              <button onClick={cerrarPopup}>Cerrar</button>
            </p>
          </div>
          <div
            style={{
              position: "fixed",
              top: 0,
              left: 0,
              width: "100%",
              height: "100%",
              background: "rgba(0, 0, 0, 0.5)",
              zIndex: 999,
            }}
            onClick={cerrarPopup}
          />
        </>
      )}

      {/* Reproductor */}
      <div
        style={{
          position: "absolute",
          top: 0,
          left: sidebarsVisible ? "380px" : "50px",
          right: 0,
          bottom: 0,
          zIndex: 1,
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          backgroundColor: isLoading || hasError ? "black" : "transparent",
          transition: "background-color 0.3s ease",
        }}
      >

        {/* Contenido del resto de la aplicación */}
        {currentUrl.endsWith(".m3u8") ? (
          <video ref={videoRef} controls style={{ width: "100%", height: "100%" }} autoPlay />
        ) : (
          <ReactPlayer
            url={currentUrl}
            playing={true}
            controls
            width="100%"
            height="100%"
            onReady={() => setIsLoading(false)}
            onError={() => setHasError(true)}
          />
        )}

        {!currentUrl && !isLoading && !hasError && (
          <div style={{ textAlign: "center", color: "white" }}>
            <p>Selecciona un canal para empezar.</p>
          </div>
        )}
      </div>
    </div>
  );
}

export default App;
